/* Main */

main {
	padding-top: 120px;
	padding-bottom: 120px;

	@media (max-width: 1199px) {
		padding-top: 80px;
		padding-bottom: 80px;
	}

	@media (max-width: 899px) {
		padding-top: 60px;
		padding-bottom: 58px;
	}

	@media (max-width: 599px) {
		padding-top: 40px;
		padding-bottom: 40px;
		width: 100%;
	}
}

.main-header {
	font-weight: 700;
	font-size: 54px;
	color: #31405b;

	@media (max-width: 1199px) {
		font-size: 50px;
		line-height: 1;
	}

	@media (max-width: 899px) {
		font-size: 42px;
	}

	@media (max-width: 599px) {
		font-size: 28px;

		.main & {
			font-size: 34px;
		}
	}
}

.main-row {
	display: flex;
	justify-content: space-between;
	margin-bottom: 120px;

	@media (max-width: 1199px) {
		margin-bottom: 80px;
	}

	@media (max-width: 899px) {
		margin-bottom: 60px;
		flex-direction: row-reverse;
	}

	@media (max-width: 599px) {
		margin-bottom: 40px;
		flex-direction: column-reverse;
	}
}

.main-row:last-child {
	margin-bottom: 0;
	flex-direction: row-reverse;

	@media (max-width: 599px) {
		flex-direction: column-reverse;
	}
}

.main-content {
	width: 470px;

	@media (max-width: 1199px) {
		width: 420px;
	}

	@media (max-width: 899px) {
		width: auto;
		padding-bottom: 17px;
	}

	@media (max-width: 599px) {
		padding-bottom: 0;
	}
}

.main-header-wrapper {
	margin-bottom: 20px;
}

.main-content-text {
	margin-bottom: 40px;
	font-size: 20px;
	line-height: 1.6;
	color: #31405b;
	transition: 0.5s;

	@media (max-width: 1199px) {
		margin-bottom: 27px;
		font-size: 18px;
	}

	@media (max-width: 899px) {
		font-size: 16px;
	}

	@media (max-width: 599px) {
		margin-bottom: 26px;
	}
}

.main-content-text p {
	margin-top: 0;
	margin-bottom: 15px;

	@media (max-width: 599px) {
		margin-top: 25px;
	}
}

.main-content-text a {
	transition: 0.5s;
	outline: none;
	color: #138ece;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

.br {
	display: none;

	@media (max-width: 899px) {
		display: inline;
	}
}

.main-img {
	width: 570px;

	@media (max-width: 1199px) {
		width: 420px;
	}

	@media (max-width: 899px) {
		width: 170px;
		margin-right: 30px;
		flex-shrink: 0;

		& img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	@media (max-width: 599px) {
		width: auto;
		height: 150px;
		margin-right: 0;
		margin-bottom: 20px;
	}
}
