/* Header */

.header {
	height: 800px;
	padding-top: 50px;
	position: relative;
	background-color: #265847;
	background-image: url("./../img/header/header-bg.jpg");
	background-size: cover;
	background-position: center;
	color: #ffffff;

	@media (max-width: 1199px) {
		height: 591px;
	}
}

.header-inner {
	display: flex;
	align-items: center;

	height: 100px;
	background-color: #265847;
	background-image: url("./../img/header/header-small-bg.jpg");
	background-position: center;
	background-size: cover;
	color: #ffffff;

	@media (max-width: 1199px) {
		height: 103px;
	}

	@media (max-width: 899px) {
		height: 80px;
	}
}

.header-top {
	display: flex;
	justify-content: space-between;
	width: 1170px;
	margin: 0 auto;
	align-items: center;

	@media (max-width: 1199px) {
		width: 100%;
		padding-left: 15px;
		padding-right: 15px;
		align-items: inherit;
	}
}

.header-content {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	text-align: center;
	width: 1170px;

	@media (max-width: 1199px) {
		width: 900px;
		padding-left: 15px;
		padding-right: 15px;
		top: 49%;
	}

	@media (max-width: 899px) {
		width: 100%;
		top: 46%;
	}

	@media (max-width: 599px) {
		top: 47%;
	}
}

.header-title {
	margin-top: 25px;
	margin-bottom: 25px;
	font-weight: bold;
	font-size: 60px;
	line-height: 1;

	@media (max-width: 1199px) {
		font-size: 52px;
	}

	@media (max-width: 899px) {
		margin-top: 20px;
		margin-bottom: 20px;
		font-size: 42px;
	}

	@media (max-width: 599px) {
		font-size: 32px;
		margin-bottom: 10px;
	}
}

.header-content p {
	font-size: 24px;

	@media (max-width: 899px) {
		font-size: 20px;
	}

	@media (max-width: 599px) {
		font-size: 18px;
	}
}

.header-scroll {
	position: absolute;
	bottom: 25px;
	left: 50%;
	transform: translateX(-50%);

	display: inline-block;
	padding-bottom: 34px;

	font-size: 16px;
	text-align: center;
	text-decoration: none;
	color: #ffffff;

	outline: none;
	transition: 0.5s;

	@media (max-width: 1199px) {
		font-size: 0;
	}
}

@keyframes scroll-down {
	0% {
		bottom: 10px;
	}
	100% {
		bottom: -10px;
	}
}

.header-scroll::after {
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);

	content: "";
	display: block;
	width: 18px;
	height: 24px;
	background-image: url("../img/header/arrow-down.svg");
}

.header-scroll:hover,
.header-scroll:focus {
	text-shadow: 2px 5px 8px #020404;
}
.header-scroll::after {
	animation: scroll-down 0.7s linear 0.1s infinite alternate;
}
