.header-nav {
	font-size: 22px;

	@media (max-width: 1199px) {
		z-index: 97;
		position: fixed;
		top: -100%;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(15, 32, 26, 0.9);
		opacity: 1;

		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;

		font-size: 22px;
		color: #ffffff;
		text-align: center;

		transition: opacity 0.2s ease-in, top 0.2s linear;
	}

	@media (max-width: 899px) {
		font-size: 20px;
	}

	@media (max-width: 599px) {
		font-size: 18px;
	}
}

.header-nav-list {
	display: flex;

	@media (max-width: 1199px) {
		flex-direction: column;
	}
}

.header-nav-list-item {
	margin-right: 50px;

	@media (max-width: 1199px) {
		margin-bottom: 20px;
		margin-right: 0px;
	}

	@media (max-width: 599px) {
		margin-bottom: 10px;
	}
}

.nav__top {
	display: none;

	@media (max-width: 1199px) {
		display: inline;
		margin-bottom: 50px;
		font-weight: 700;
		font-size: 24px;
		line-height: 1;
		text-transform: uppercase;
		color: #0ac883;
	}

	@media (max-width: 899px) {
		margin-bottom: 40px;
		font-size: 20px;
	}

	@media (max-width: 599px) {
		margin-bottom: 30px;
	}
}

.header-nav-list-item:last-child {
	margin-right: 0;
}

.header-nav-link {
	color: #ffffff;
	text-decoration: none;
	transition: 0.5s;
	outline: none;

	@media (max-width: 1199px) {
		transition: color 0.2s ease-in;

		&:hover {
			color: #0ac883;
		}
	}
}

.header-nav-link:hover,
.header-nav-link:focus {
	text-shadow: 2px 3px 8px #020404;
	color: #0ac883;
}

.nav--active {
	z-index: 98;
	top: 0;
	opacity: 1;
	transition: top 0.2s linear, opacity 0.2s ease-in;
}
