/* Inner Page Tours */

.inner-page {
	padding-top: 60px;
	padding-bottom: 100px;

	&.article-page {
		padding-top: 70px;
	}

	@media (max-width: 1199px) {
		padding-top: 48px;
		padding-bottom: 58px;

		&.tours-page {
			padding-top: 68px;
		}

		&.article-page {
			padding-top: 46px;
			padding-bottom: 63px;
		}
	}

	@media (max-width: 899px) {
		padding-top: 47px;
		padding-bottom: 40px;

		&.tours-page {
			padding-top: 46px;
			padding-bottom: 40px;
		}

		&.article-page {
			padding-top: 28px;
			padding-bottom: 58px;
		}
	}

	@media (max-width: 599px) {
		padding-top: 40px;
		padding-bottom: 40px;

		&.tours-page {
			padding-top: 40px;
		}

		&.article-page {
			padding-top: 25px;
			padding-bottom: 40px;
		}
	}
}

.inner-page-tours-header {
	margin-bottom: 60px;
	text-align: center;

	@media (max-width: 1199px) {
		margin-bottom: 47px;
	}

	@media (max-width: 599px) {
		margin-bottom: 30px;
	}
}
