/* Contacts */

.contacts-header {
	margin-bottom: 40px;

	@media (max-width: 1199px) {
		margin-bottom: 47px;
	}

	@media (max-width: 899px) {
		margin-bottom: 35px;
	}

	@media (max-width: 599px) {
		margin-bottom: 30px;
	}
}

.contact-row {
	display: flex;
	justify-content: space-between;
	align-items: flex-start;

	@media (max-width: 1199px) {
		flex-direction: column;
	}
}

.contact-info {
	width: 470px;

	@media (max-width: 1199px) {
		margin-bottom: 40px;
		display: flex;
		width: 100%;
		justify-content: space-between;
	}

	@media (max-width: 899px) {
		display: block;
	}

	@media (max-width: 599px) {
		margin-bottom: 30px;
	}
}

.contact-block + .contact-block {
	margin-top: 44px;

	@media (max-width: 1199px) {
		margin-top: 0;
	}

	@media (max-width: 899px) {
		margin-top: 43px;
	}

	@media (max-width: 599px) {
		margin-top: 32px;
	}
}

.contact-block-title {
	margin-bottom: 20px;
	font-weight: bold;
	font-size: 28px;

	@media (max-width: 1199px) {
		margin-bottom: 28px;
		font-size: 24px;
	}

	@media (max-width: 599px) {
		margin-bottom: 20px;
		font-size: 20px;
	}
}

.contact-block-body {
	font-size: 20px;

	@media (max-width: 1199px) {
		font-size: 18px;
	}

	@media (max-width: 599px) {
		font-size: 16px;
	}
}

.contact-map {
	width: 470px;
	height: 345px;

	@media (max-width: 1199px) {
		width: 420px;
		height: 310px;
	}

	@media (max-width: 899px) {
		width: 100%;
	}

	@media (max-width: 599px) {
		height: 157px;
	}
}

.contact-list li {
	margin-bottom: 21px;
}

.contact-list a {
	color: #138ece;
	text-decoration: none;
	transition: 0.5s;
	outline: none;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

.contact-list-icon {
	position: relative;
	padding-left: 39px;

	@media (max-width: 599px) {
		padding-left: 34px;
	}
}

.contact-list-icon::before {
	content: "";
	display: block;
	top: 3px;
	left: 0;
	width: 24px;
	height: 24px;
	position: absolute;
}

.contact-list-icon.phone::before {
	background-image: url("./../img/icons/phone.svg");
}

.contact-list-icon.mail::before {
	background-image: url("./../img/icons/mail.svg");
}

.contact-list-icon.address::before {
	background-image: url("./../img/icons/map-pin.svg");
}
