/* Footer */

.footer {
	margin-top: auto;
	padding-top: 70px;
	padding-bottom: 90px;
	background-color: #265847;
	color: #fff;

	@media (max-width: 1199px) {
		padding-top: 50px;
		padding-bottom: 50px;
	}

	@media (max-width: 899px) {
		padding-top: 31px;
		padding-bottom: 40px;
	}

	@media (max-width: 599px) {
		text-align: center;
	}
}

.footer a {
	color: #fff;
	text-decoration: none;
	outline: none;
	transition: 0.5s;
}

.footer-row {
	display: flex;
	justify-content: space-between;

	@media (max-width: 899px) {
		flex-wrap: wrap;
		flex-direction: column-reverse;
	}

	@media (max-width: 599px) {
		flex-direction: column-reverse;
	}
}

.footer-widgets-wrapper {
	display: flex;
	padding-top: 9px;

	@media (max-width: 899px) {
		width: 100%;
		margin-bottom: 50px;
		justify-content: space-between;
	}

	@media (max-width: 599px) {
		flex-direction: column;
		align-items: center;
		margin-bottom: 0;
	}
}

.footer-widgets-wrapper > * {
	@media (max-width: 899px) {
		width: calc(50% - 15px);
	}

	@media (max-width: 599px) {
		width: 100%;
	}
}

.footer-copyright {
	width: 270px;

	@media (max-width: 1199px) {
		width: 195px;
	}

	@media (max-width: 899px) {
		width: auto;
	}
}

.footer-logo {
	margin-bottom: 26px;

	@media (max-width: 1199px) {
		margin-bottom: 18px;

		& .logo {
			font-size: 30px;
		}
	}

	@media (max-width: 899px) {
		margin-bottom: 20px;
	}

	@media (max-width: 599px) {
		margin-bottom: 10px;
	}
}

.footer-copyright p {
	font-size: 16px;
	line-height: 1.67;

	@media (max-width: 1199px) {
		font-size: 14px;
	}

	@media (max-width: 899px) {
		font-size: 14px;
	}
}

.footer-widget {
	font-size: 18px;

	@media (max-width: 599px) {
		margin-bottom: 46px;
		font-size: 16px;
	}
}

.footer-widget a:hover,
.footer-widget a:focus {
	text-shadow: 5px 5px 5px #020404;
	text-decoration: none;
}

.footer-widget-info {
	width: 170px;
}

.footer-widget-services,
.footer-widget-contacts {
	width: 270px;
}

.footer-widget-services {
	@media (max-width: 1199px) {
		display: none;
	}

	@media (max-width: 599px) {
		display: block;
	}
}

.footer-widget + .footer-widget {
	margin-left: 30px;

	@media (max-width: 1199px) {
		margin-left: 56px;
	}

	@media (max-width: 599px) {
		margin-left: 0;
	}
}

.footer-widget p:not(:last-child) {
	margin-bottom: 15px;
}

.footer-widget-title {
	margin-bottom: 30px;
	font-weight: bold;
	font-size: 24px;

	@media (max-width: 1199px) {
		margin-bottom: 20px;
	}

	@media (max-width: 599px) {
		font-size: 20px;
		margin-bottom: 10px;
	}
}

.footer-widget-list {
	font-size: 18px;

	@media (max-width: 599px) {
		font-size: 16px;
	}
}

.footer-widget-list li:not(:last-child) {
	margin-bottom: 15px;

	@media (max-width: 599px) {
		margin-bottom: 10px;
	}
}
