/* Article Page*/

.row-articles-page {
	display: flex;
	justify-content: space-between;

	@media (max-width: 899px) {
		flex-direction: column-reverse;
		align-items: center;
	}
}

.article-page-article {
	width: 770px;
	flex-shrink: 0;
	flex-grow: 0;
	font-size: 20px;
	line-height: 1.6;
	color: #31405b;

	@media (max-width: 1199px) {
		width: 570px;
		font-size: 18px;
	}

	@media (max-width: 899px) {
		width: auto;
		font-size: 16px;
	}
}

.article-page-article h2,
.article-page-article h3,
.article-page-article h4,
.article-page-article h5,
.article-page-article h6 {
	font-weight: 700;
	line-height: 1.6;

	@media (max-width: 599px) {
		line-height: 1;
	}
}

.article-page-article h1 {
	margin-bottom: 45px;
	font-size: 54px;
	line-height: 1;

	@media (max-width: 1199px) {
		margin-bottom: 30px;
		font-size: 50px;
	}

	@media (max-width: 899px) {
		font-size: 42px;
	}

	@media (max-width: 599px) {
		margin-bottom: 15px;
		font-size: 28px;
	}
}

.article-page-article h2 {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size: 32px;

	@media (max-width: 1199px) {
		margin-top: 26px;
	}

	@media (max-width: 599px) {
		margin-top: 20px;
		margin-bottom: 20px;
	}
}

.article-page-article h3 {
	margin-top: 20px;
	margin-bottom: 10px;
	font-size: 26px;

	@media (max-width: 1199px) {
		margin-top: 23px;
		margin-bottom: 13px;
	}

	@media (max-width: 599px) {
		margin-top: 20px;
		margin-bottom: 20px;
	}
}

.article-page-article h4 {
	margin-top: 15px;
	margin-bottom: 15px;
	font-size: 23px;

	@media (max-width: 599px) {
		margin-top: 19px;
		margin-bottom: 11px;
		line-height: 1.6;
	}
}

.article-page-article h5 {
	margin-top: 25px;
	margin-bottom: 20px;
	font-size: 20px;
}

.article-page-article h6 {
	margin-top: 20px;
	margin-bottom: 18px;
	font-size: 18px;
}

.article-page-article p {
	margin-bottom: 16px;

	@media (max-width: 1199px) {
		margin-bottom: 20px;
	}

	@media (max-width: 599px) {
		margin-bottom: 10px;
	}

	& + p {
		@media (max-width: 599px) {
			margin-top: 24px;
		}
	}
}

.article-page-article img {
	margin-top: 40px;
	margin-bottom: 40px;

	@media (max-width: 1199px) {
		margin-top: 30px;
		margin-bottom: 30px;
	}

	@media (max-width: 899px) {
		width: 100%;
		margin-top: 20px;
		margin-bottom: 20px;
	}

	@media (max-width: 599px) {
		margin-top: 28px;
	}
}

.article-page-article a {
	transition: 0.5s;
	outline: none;
	color: #138ece;

	&:hover,
	&:focus {
		color: #265847;
	}
}

.article-page-article > *:last-child {
	margin-bottom: 0;
}
