/* Contact form */

.form {
	padding: 40px;
	width: 630px;
	background: #ffffff;
	box-shadow: 0px 10px 20px rgba(211, 211, 211, 0.25);

	@media (max-width: 1199px) {
		width: 100%;
		padding-left: 40px;
		padding-right: 40px;
		padding-top: 32px;
		padding-bottom: 42px;
	}

	@media (max-width: 899px) {
		padding-left: 30px;
		padding-right: 30px;
		padding-top: 21px;
		padding-bottom: 30px;
	}

	@media (max-width: 599px) {
		padding-left: 15px;
		padding-right: 17px;
		padding-top: 19px;
		padding-bottom: 30px;
	}
}

.form-title {
	margin-bottom: 20px;
	font-weight: 700;
	font-size: 28px;

	@media (max-width: 599px) {
		font-size: 24px;
	}
}

.form-group-wrapper {
	margin-bottom: 19px;

	@media (max-width: 899px) {
		margin-bottom: 15px;
	}

	@media (max-width: 599px) {
		margin-bottom: 20px;
	}
}

.form-group {
	margin-bottom: 20px;
	font-size: 18px;

	@media (max-width: 599px) {
		margin-bottom: 14px;
		font-size: 16px;
	}
}

.form-group:last-of-type {
	margin-bottom: 0px;
}

.form-group-title {
	margin-bottom: 15px;
	font-weight: 700;
	font-size: 20px;

	@media (max-width: 599px) {
		margin-bottom: 8px;
		font-size: 18px;
	}
}

.form-group-row {
	margin-bottom: 10px;
	display: flex;
	justify-content: space-between;

	@media (max-width: 599px) {
		display: block;
	}
}

.form-group-row > .input + .input {
	margin-left: 10px;

	@media (max-width: 599px) {
		margin-left: 0;
		margin-top: 10px;
	}
}

.input {
	width: 100%;
	height: 50px;
	padding-left: 20px;
	padding-right: 20px;

	background: #eeeeee;
	border: 1px solid #c0c0c0;
	border-radius: 6px;

	font-size: 18px;
	color: #31405b;

	@media (max-width: 1199px) {
		padding-left: 29px;
		padding-right: 29px;
	}

	@media (max-width: 899px) {
		padding-left: 18px;
		padding-right: 18px;
	}

	@media (max-width: 599px) {
		padding-left: 15px;
		padding-right: 15px;
	}
}

.input::placeholder {
	color: rgba(49, 64, 91, 0.5);
}

.input:focus {
	outline: none;
	border: 2px solid #0ac883;
	box-shadow: 0px 2px 8px rgba(39, 105, 94, 0.5);
}
