/* Card */

.cards-first-row {
	display: flex;
	justify-content: space-between;
	margin-bottom: 30px;

	@media (max-width: 899px) {
		display: block;
	}

	@media (max-width: 599px) {
		margin-bottom: 20px;
	}
}

.cards-first-row > * + * {
	@media (max-width: 899px) {
		margin-top: 30px;
	}

	@media (max-width: 599px) {
		margin-top: 20px;
	}
}

.cards-second-row {
	display: flex;
	justify-content: space-between;

	@media (max-width: 899px) {
		display: block;

		& > * + * {
			margin-top: 30px;
		}
	}

	@media (max-width: 599px) {
		& > * + * {
			margin-top: 20px;
		}
	}
}

.cards-second-row-column > * + * {
	margin-top: 30px;

	@media (max-width: 1199px) {
		margin-top: 30px;
	}

	@media (max-width: 899px) {
		margin-top: 30px;
	}

	@media (max-width: 599px) {
		margin-top: 20px;
	}
}

.card {
	display: block;
	width: 570px;
	color: #fff;
	border: 2px solid transparent;
	text-decoration: none;
	transition: 0.5s;
	outline: none;

	@media (max-width: 1199px) {
		width: 420px;
	}

	@media (max-width: 899px) {
		width: 570px;
		margin-right: auto;
		margin-left: auto;
	}

	@media (max-width: 599px) {
		width: auto;
	}
}

.card:hover {
	opacity: 0.9;
}

.card:focus {
	border: 2px solid #265847;
}

.card.bigger {
	width: 670px;

	@media (max-width: 1199px) {
		width: 495px;
	}

	@media (max-width: 899px) {
		width: 570px;
		margin-right: auto;
		margin-left: auto;
	}

	@media (max-width: 599px) {
		width: auto;
	}
}

.card.small {
	width: 470px;

	@media (max-width: 1199px) {
		width: 345px;
	}

	@media (max-width: 899px) {
		width: 570px;
		margin-right: auto;
		margin-left: auto;
	}

	@media (max-width: 599px) {
		width: auto;
	}
}

.card img {
	width: 100%;
	object-fit: cover;

	@media (max-width: 1199px) {
		height: 240px;
	}
}

.card.bigger img {
	width: 100%;
	object-fit: cover;

	@media (max-width: 1199px) {
		height: 210px;
	}
}

.card.small img {
	width: 100%;
	object-fit: cover;

	@media (max-width: 1199px) {
		height: 100%;
	}

	@media (max-width: 899px) {
		height: 210px;
	}
}

.card-title-wrapper {
	display: flex;
	align-items: center;
	height: 80px;
	padding-left: 30px;
	padding-right: 30px;

	background-color: #a6a6a6;
	color: #ffffff;

	white-space: nowrap;

	@media (max-width: 1199px) {
		height: 60px;
		padding-left: 20px;
		padding-right: 20px;
	}

	@media (max-width: 899px) {
		padding-left: 25px;
		padding-right: 25px;
	}

	@media (max-width: 599px) {
		padding-left: 15px;
		padding-right: 15px;
		height: 50px;
	}
}
.card-title {
	font-size: 28px;
	font-weight: 700;

	text-overflow: ellipsis;
	overflow: hidden;

	@media (max-width: 1199px) {
		font-size: 24px;
	}

	@media (max-width: 599px) {
		font-size: 20px;
	}
}

.card.extreme-color .card-title-wrapper {
	background-color: #c1b19b;
}

.card.adventures-color .card-title-wrapper {
	background-color: #66b0be;
}

.card.tracking-color .card-title-wrapper {
	background-color: #876f56;
}

.card.jungle-color .card-title-wrapper {
	background-color: #27695e;
}
