/* Checkbox */

.checkbox-row {
	display: flex;
	align-items: flex-start;
	margin-bottom: 16px;

	@media (max-width: 599px) {
		margin-bottom: 23px;

		&:last-child {
			margin-bottom: 0;
		}
	}
}

.real-checkbox {
	width: 0;
	height: 0;
	position: absolute;
	opacity: 0;
	z-index: -1;
	appearance: none;
}

.custom-checkbox {
	position: relative;
	display: inline-block;
	flex-shrink: 0;

	margin-right: 14px;
	width: 20px;
	height: 20px;
	vertical-align: bottom;

	background: #eeeeee;
	border: 1px solid #c0c0c0;
	border-radius: 2px;
}

.custom-checkbox::before {
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%) scale(0);
	content: "";
	width: 17px;
	height: 17px;
	background-image: url("../img/icons/check.svg");
	transition: transform 0.2s ease-in;
}

.real-checkbox:focus + .custom-checkbox {
	border: 2px solid #0ac883;
	box-shadow: 0px 2px 8px rgba(39, 105, 94, 0.5);
}

.real-checkbox:checked + .custom-checkbox::before {
	transform: translate(-50%, -50%) scale(1);
}
