/* Article Page*/

.article-page-nav {
	width: 269px;
	flex-shrink: 0;
	flex-grow: 0;

	@media (max-width: 899px) {
		margin-top: 40px;
		width: 100%;
	}

	@media (max-width: 599px) {
		margin-top: 35px;
	}
}

.article-page-nav-title {
	margin-bottom: 34px;
	font-weight: 700;
	font-size: 28px;
	color: #31405b;

	@media (max-width: 899px) {
		margin-bottom: 20px;
	}

	@media (max-width: 599px) {
		margin-bottom: 15px;
		font-size: 22px;
	}
}

.article-page-nav-list {
	border-top: 1px solid #d6d9d8;
	font-size: 20px;
	line-height: 26px;

	@media (max-width: 1199px) {
		font-size: 18px;
		line-height: 23px;
	}

	@media (max-width: 599px) {
		font-size: 16px;
		line-height: 21px;
	}
}

.article-page-nav-list li {
	border-bottom: 1px solid #d6d9d8;
}

.article-page-nav-list a {
	display: block;
	padding-top: 14px;
	padding-bottom: 15px;
	color: #138ece;
	text-decoration: none;
	transition: 0.5s;
	outline: none;

	@media (max-width: 599px) {
		padding-top: 5px;
		padding-bottom: 9px;
	}
}

.article-page-nav-list a.active {
	font-weight: 700;
	color: #e04e4e;
}

.article-page-nav-list a:hover,
.article-page-nav-list a:focus {
	text-decoration: underline;
}
