html,
body,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
dl,
dt,
dd,
ol,
ul,
li {
	margin: 0;
	padding: 0;
	border: 0;
	list-style-type: none;
}

*,
*::before,
*::after {
	box-sizing: border-box;
}

fieldset {
	border: none;
	margin: 0;
	padding: 0;
}

legend {
	display: block;
	margin: 0;
	padding: 0;
	border: 0;
}

html {
	height: 100%;
	scroll-behavior: smooth;
}

body {
	display: flex;
	flex-direction: column;
	height: 100%;
	font-family: "PT Sans", sans-serif;
	color: #31405b;
	background-color: #f2f8f6;
}

body > * {
	flex-shrink: 0;
}

input {
	font-family: "PT Sans", sans-serif;
}

label {
	cursor: pointer;
}

img {
	display: block;
	max-width: 100%;
	height: auto;
}

.none {
	display: none;
}

.container {
	width: 1200px;
	margin: 0 auto;
	padding-left: 15px;
	padding-right: 15px;

	@media (max-width: 1199px) {
		width: 900px;
	}

	@media (max-width: 899px) {
		width: 100%;
	}
}

.bgcolor {
	background-color: #fff;
}
