/* Button */

.button {
	display: inline-block;
	height: 50px;
	padding-left: 41px;
	padding-right: 41px;

	background: #0ac883;
	box-shadow: 0px 8px 20px rgba(24, 219, 148, 0.35);
	border-radius: 50px;
	border: none;

	font-family: PT Sans;
	font-weight: 700;
	font-size: 20px;
	line-height: 50px;
	text-align: center;
	text-decoration: none;
	color: #ffffff;

	transition: 0.5s;
	outline: none;
	cursor: pointer;

	@media (max-width: 899px) {
		padding-left: 35px;
		padding-right: 35px;
		height: 46px;
		font-size: 18px;
		line-height: 46px;
	}

	@media (max-width: 599px) {
		padding-left: 35px;
		padding-right: 35px;
	}

	.form & {
		@media (max-width: 899px) {
			padding-left: 30px;
			padding-right: 30px;
		}

		@media (max-width: 599px) {
			padding-left: 35px;
			padding-right: 35px;
		}
	}
}

.button:hover,
.button:focus {
	background: #265847;
	box-shadow: 0px 8px 20px rgba(39, 105, 94, 0.5);
}
