/* Radio */

.radio-row {
	display: flex;
	align-items: flex-start;
	margin-bottom: 20px;

	@media (max-width: 599px) {
		margin-bottom: 9px;
	}
}

.radio-row:last-of-type {
	margin-bottom: 10px;
}

.real-radio {
	width: 0;
	height: 0;
	position: absolute;
	opacity: 0;
	z-index: -1;
	appearance: none;
}

.custom-radio {
	position: relative;
	display: inline-block;
	flex-shrink: 0;

	margin-right: 14px;
	vertical-align: bottom;
	width: 20px;
	height: 20px;

	background: #eeeeee;
	border: 1px solid #c0c0c0;
	border-radius: 50%;
}

.real-radio:focus + .custom-radio {
	border: 2px solid #0ac883;
	box-shadow: 0px 2px 8px rgba(39, 105, 94, 0.5);
}

.real-radio:checked + .custom-radio::before {
	transform: translate(-50%, -50%) scale(1);
}

.custom-radio::before {
	position: absolute;
	display: block;
	content: "";
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%) scale(0);

	width: 12px;
	height: 12px;
	background: #2ab698;
	border-radius: 50%;

	transition: transform 0.2s ease-in;
}
